/* Ensure consistent row heights and borders */
tr {
  height: 31px;
  border-bottom: 1px solid #dee2e6;
}

thead tr {
  height: 31px;
}

/* Add padding to cells for better spacing */
td, th {
  padding: 4px 8px;
  vertical-align: middle;
}

/* Add loading spinner */
.loading-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-size: 1.2rem;
  color: #666;
}

/* Error styling */
.error-container {
  text-align: center;
  padding: 2rem;
  margin: 2rem;
  background-color: #fff3f3;
  border-radius: 8px;
  border: 1px solid #ffcdd2;
}

.error-container button {
  padding: 8px 16px;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 1rem;
}

/* Add responsive design improvements */
@media (max-width: 768px) {
  .form-row {
    flex-direction: column;
  }
  
  .form-group {
    width: 100%;
    margin-right: 0;
  }
  
  .modal-content {
    width: 95%;
    margin: 10px;
  }
}
